* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #af9999c5;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
}

.short-desc {
  width: 400px;
  background-color: #d2e757;
  border-radius: 4px;
  margin-top: 10px;
  padding: 15px;
}

.country-item {
  display: flex;
  align-items: center;
}

.country-item,
.country {
  width: 400px;
  background-color: #d2e757;
  border-radius: 4px;
  padding: 15px;
}

.capital-flag {
  width: 300px;
  height: 150px;
}

.country-flag {
  width: 70px;
  height: 50px;
  margin-right: 15px;
}

.country-name,
.desc-text {
  font-size: 25px;
  font-weight: 500;
  color: #851313;
}